textarea {
  scrollbar-width: none; /* Firefox */
  -ms-overflow-style: none; /* IE и Edge */
}
textarea::-webkit-scrollbar {
  display: none; /* Chrome, Safari и Opera */
}

.textarea-scrollbar {
  position: absolute;
  z-index: 10;
  top: 1rem;
  bottom: 1rem;
  right: 0.5rem;
  width: 2.4rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 4px 0;
  transition: opacity 0.3s ease-in;
}
.textarea-scrollbar.disabled {
  opacity: 0;
}
.textarea-scrollbar.disabled .textarea-scroll-track {
  background-color: #888c8b;
}
.textarea-scrollbar.disabled .textarea-scroll-btn {
  color: #888c8b;
}

.textarea-scroll-btn {
  width: 2.4rem;
  height: 2.4rem;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #000;
  transition: background-color 0.3s ease-in;
}
.textarea-scroll-btn svg {
  fill: currentColor;
}
.textarea-scroll-btn:disabled, .textarea-scroll-btn.disabled {
  color: #888c8b;
  cursor: default;
}
.textarea-scroll-btn svg {
  width: 0.8rem;
  height: 0.8rem;
  fill: currentColor;
}

.textarea-scroll-track {
  position: relative;
  flex-grow: 1;
  width: 2px;
  background: #000;
  border-radius: 100px;
  margin: 0.4rem 0;
  border-radius: 0.1rem;
  overflow: hidden;
  transition: background-color 0.3s ease-in;
}

.textarea-scroll-thumb {
  position: absolute;
  left: 50%;
  width: 100%;
  min-height: 2rem;
  background: transparent;
  opacity: 0.3;
  border-radius: 0.2rem;
  transform: translateX(-50%);
  cursor: pointer;
  transition: opacity 0.3s;
}

.dropdown {
  display: block;
  width: 100%;
  border-radius: 1rem;
  padding: 2.68rem 2rem;
  background-color: #fafafa;
  border: none;
  font-weight: 400;
  font-size: 2.2rem;
  line-height: 120%;
  transition: color 0.1s ease-out, background-color 0.1s ease-out, border-radius 0.1s ease-out;
  position: relative;
  z-index: 15;
}
.dropdown__header {
  position: relative;
  color: rgba(0, 0, 0, 0.45);
  transition: color 0.3s ease;
}
.dropdown__btn {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  width: 2.2rem;
  height: 1.3rem;
  flex-shrink: 0;
  transition: color 0.3s ease-in, transform 0.3s ease-in;
}
.dropdown__btn svg {
  display: block;
  width: 100%;
  height: 100%;
  fill: none;
  stroke-width: 3px;
  stroke-linecap: round;
  stroke: currentColor;
}
.dropdown__label {
  position: absolute;
  top: 0rem;
  color: currentColor;
  transition: color 0.3s ease, transform 0.3s ease, font-size 0.3s ease;
}
.dropdown__capture {
  opacity: 0;
  color: currentColor;
  transition: color 0.3s ease, opacity 0.3s ease-in;
}
.dropdown__body {
  position: absolute;
  z-index: 4;
  left: 0px;
  top: 100%;
  right: 0px;
