.mobile-menu {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  z-index: 6;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.mobile-menu.menu--active {
  opacity: 1;
  visibility: visible;
}
.mobile-menu__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}
.mobile-menu__content {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  background-color: var(--ac-white);
  padding: 3rem;
  padding-top: 14rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  transform: translateY(-100%);
  transition: transform 0.3s ease;
}
.menu--active .mobile-menu__content {
  transform: translateY(0);
}
.mobile-menu__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 4rem;
  padding-bottom: 2rem;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}
@media (max-width: 600px) {
  .mobile-menu__header {
    margin-bottom: 3rem;
    padding-bottom: 1.5rem;
  }
}
.mobile-menu__logo {
  display: flex;
  align-items: center;
  gap: 1rem;
  text-decoration: none;
  color: var(--ac-black);
  font-weight: 500;
  font-size: 2rem;
}
.mobile-menu__logo img {
  width: 4rem;
  height: 2.1rem;
  flex-shrink: 0;
}
.mobile-menu__actions {
  display: flex;
  align-items: center;
  gap: 2rem;
}
.mobile-menu__lk {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
  background-color: var(--ac-pink);
  transition: background-color 0.3s ease;
}
.mobile-menu__lk img {
  width: 2rem;
  height: 2rem;
  filter: brightness(0) invert(1);
}
@media (any-hover: hover) {
  .mobile-menu__lk:hover {
    background-color: rgb(167.4713656388, 8.5286343612, 51.1718061674);
  }
}
.mobile-menu__close {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 4rem;
  height: 4rem;
  border: none;
  background: none;
  color: var(--ac-pink);
  cursor: pointer;
  transition: color 0.3s ease;
}
@media (any-hover: hover) {
  .mobile-menu__close:hover {
    color: rgb(167.4713656388, 8.5286343612, 51.1718061674);
  }
}
.mobile-menu__nav {
  flex: 1;
  margin-bottom: 8rem;
}
.mobile-menu__nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  gap: 4rem;
}
.mobile-menu__nav li a {
  display: block;
  font-weight: 400;
  font-size: 2.2rem;
  color: var(--ac-black);
  text-decoration: none;
  transition-property: color;
  transition-duration: 0.3s;
}
@media (any-hover: hover) {
  .mobile-menu__nav li a:hover {
    color: var(--ac-pink);
  }
}
.mobile-menu__nav li a:focus {
  outline: none;
  color: var(--ac-pink);
}
.mobile-menu__cta {
  width: 100%;
  font-size: 2.6rem;
  padding: 1.3rem 2rem 1.6rem;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1000;
  display: none;
  opacity: 0;
  visibility: hidden;
  transition: all 0.3s ease;
}
.modal.modal--active {
  display: flex;
  opacity: 1;
  visibility: visible;
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(4px);
}
.modal__container {
  position: relative;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2rem;
}
@media (max-width: 600px) {
  .modal__container {
    padding: 1rem;
  }
}
.modal__content {
  position: relative;
  background-color: transparent;
  max-width: 180rem;
  width: 100%;
  max-height: 90vh;
  overflow: hidden;
}
@media (max-width: 600px) {
  .modal__content {
    max-height: 95vh;
  }
}
.modal__close {
  position: absolute;
  top: 2rem;
  right: 2rem;
  width: 3rem;
  height: 3rem;
  border: none;
  cursor: pointer;
  z-index: 10;
  display: flex;
  align-items: center;
  justify-content: center;
  color: var(--ac-black);
  transition: all 0.3s ease;
  background: none;
  padding: 0;
}
.modal__close svg {
  width: 100%;
  height: 100%;
  stroke-width: 2px;
}
.modal .cta__container {
  display: flex;
  position: relative;
  gap: 2rem;
  border-radius: 1rem;
}
@media (max-width: 600px) {
  .modal .cta__container {
    flex-direction: column;
    gap: 1rem;
  }
}
.modal .cta__content {
  padding: 7.5rem 8rem 9rem;
  max-width: 107.3rem;
  width: 100%;
  flex-shrink: 0;
  background-color: var(--ac-white);
  border-radius: 1rem;
  overflow: hidden;
}
@media (max-width: 600px) {
  .modal .cta__content {
    padding: 5rem 3rem 6rem;
    max-width: 100%;
  }
}
.modal .cta__decor {
  flex-grow: 1;
  background-color: var(--ac-white);
  position: relative;
  background-image: url("./../img/lines.svg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: right;
}
@media (max-width: 600px) {
  .modal .cta__decor {
    display: none;
  }
}
.modal .cta__decor img {
  position: absolute;
  bottom: 0;
  right: 0;
  max-width: 79rem;
  height: auto;
}

.modal--active .modal__content {
  animation: modalSlideIn 0.3s ease-out;
}

@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-2rem) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
[data-target] {
  cursor: pointer;
}

body.modal-open {
  overflow: hidden;
}

